@import './Palette.scss';

.home {
  height: 100%;
  width: 100%;
}

.subcat-hover {
  transition: color .4s background-color .4s opacity .4s;
  cursor: pointer;
  background-color: $box-shadow-dark;
  color: #ffffff;
  font-size: 48px;
}

.vxx-hover {
  opacity: 0;
  transition: opacity .4s;
  cursor: pointer;
}

.vxx-hover:hover {
  opacity: 1;
}

.fade {
  transition: opacity .8s;
  z-index: 0;
}

